/* Form */

form label {
    display: block;
    color: #252122;
    font-weight: 400;
    margin: 0 0 0.5em 0;
}

form input[type=text],
form input[type=email],
form input[type=password],
form select,
form textarea {
    -webkit-appearance: none;
    display: block;
    border: 0;
    padding: 0.75em 1em;
    font-size: 1em;
    border-radius: 5px;
    border: solid 1px #dddddd;
    background: #fff;
    width: 100%;
}

form input[type=text]:focus,
form input[type=email]:focus,
form input[type=password]:focus,
form select:focus,
form textarea:focus {
    box-shadow: inset 0px 0px 1px 1px #43bff0;
}

form textarea {
    height: 15em;
}

form ::-webkit-input-placeholder {
    color: #555 !important;
}

form :-moz-placeholder {
    color: #555 !important;
}

form ::-moz-placeholder {
    color: #555 !important;
}

form :-ms-input-placeholder {
    color: #555 !important;
}

form ::-moz-focus-inner {
    border: 0;
}

/* Tables */

table {
    width: 100%;
}

table tr {
    border-top: solid 1px #dddddd;
}

table tr:first-child {
    border-top: 0;
}

table td {
    padding: 0.5em 1em 0.5em 1em;
}

table th {
    text-align: left;
    padding: 0.5em 1em 0.5em 1em;
    font-weight: 700;
    margin: 0 0 1em 0;
}

table thead {
    border-bottom: solid 2px #dddddd;
}

/* Section/Article */

// section,
// article {
//     margin-bottom: 2.5em;
// }

section> :last-child,
section:last-child,
article> :last-child,
article:last-child {
    margin-bottom: 0;
}

header {
    margin: 0 0 1.5em 0;
}

header>p {
    display: block;
    font-style: italic;
    margin: 0.5em 0 0 0;
    padding-bottom: 0.5em;
}

header.major {
    position: relative;
    text-align: center;
    border-top: solid 1px #ccc;
    top: 1em;
    margin: 0 0 3em 0;
}

header.major h2 {
    background: #fff;
    position: relative;
    top: -0.65em;
    display: inline;
    margin: 0;
    padding: 0 1em 0 1em;
}

footer {
    margin: 2.25em 0 0 0;
}

/* Box */

.box {
    background: #ffffff;
    border-bottom: solid 1px #ccc;
    padding: 2.75em 1.75em 2.75em 1.75em;
}

.box .image.featured {
    position: relative;
    width: auto;
    left: 1.75em;
    top: 1.75em;
    margin: -4.5em 0 4.25em -3.5em;
}

.box.post header {
    padding-top: 1em;
    margin: 0 0 2em 0;
}

.box.post h2 {
    font-size: 2.5em;
    letter-spacing: -0.015em;
}

.box.post header>p {
    margin-top: 1.25em;
    font-size: 1.25em;
}

.box footer ul.actions {
    margin-bottom: 0;
}

/* Button */

input[type="button"],
input[type="submit"],
input[type="reset"],
button,
.button {
    -webkit-appearance: none;
    display: inline-block;
    text-decoration: none;
    cursor: pointer;
    border: 0;
    border-radius: 5px;
    background: #d52349;
    color: #fff !important;
    font-weight: 700;
    outline: 0;
    font-size: 1.1em;
    padding: 0.65em 1.5em 0.65em 1.5em;
    text-align: center;
    -moz-transition: background-color .25s ease-in-out;
    -webkit-transition: background-color .25s ease-in-out;
    -ms-transition: background-color .25s ease-in-out;
    transition: background-color .25s ease-in-out;
}

input[type="button"]:hover,
input[type="submit"]:hover,
input[type="reset"]:hover,
button:hover,
.button:hover {
    background: #e53359;
}

input[type="button"]:active,
input[type="submit"]:active,
input[type="reset"]:active,
button:active,
.button:active {
    background: #c51349;
}

input[type="button"].alt,
input[type="submit"].alt,
input[type="reset"].alt,
button.alt,
.button.alt {
    background: #252122;
}

input[type="button"].alt:hover,
input[type="submit"].alt:hover,
input[type="reset"].alt:hover,
button.alt:hover,
.button.alt:hover {
    background: #353132;
}

input[type="button"].alt:active,
input[type="submit"].alt:active,
input[type="reset"].alt:active,
button.alt:active,
.button.alt:active {
    background: #151112;
}

input[type="button"].icon:before,
input[type="submit"].icon:before,
input[type="reset"].icon:before,
button.icon:before,
.button.icon:before {
    opacity: 0.35;
    position: relative;
    top: 0.05em;
    margin-right: 0.75em;
}

input[type="button"].large,
input[type="submit"].large,
input[type="reset"].large,
button.large,
.button.large {
    font-size: 1.5em;
    padding: 0.75em 1.5em 0.75em 1.5em;
}

/* Image */

.image {
    display: inline-block;
    outline: 0;
}

.image img {
    display: block;
    width: 100%;
}

.image.centered {
    display: block;
    margin: 0 0 2em 0;
}

.image.centered img {
    margin: 0 auto;
    width: auto;
}

.image.featured {
    display: block;
    width: 100%;
    margin: 0 0 2em 0;
}

.image.left {
    float: left;
    margin: 0 2em 2em 0;
}

/* List */

ul {
    list-style: disc;
    padding-left: 1em;
}

ul li {
    padding-left: 0.5em;
}

ol {
    list-style: decimal;
    padding-left: 1.25em;
}

ol li {
    padding-left: 0.25em;
}

/* Actions */

ul.actions {
    list-style: none;
    padding-left: 0;
}

ul.actions li {
    display: inline-block;
    margin-left: 1em;
    padding-left: 0;
}

ul.actions li:first-child {
    margin-left: 0;
}

/* Links */

ul.links {
    list-style: none;
    padding-left: 0;
}

ul.links li {
    display: inline-block;
    border-left: solid 1px rgba(255, 255, 255, 0.05);
    padding: 0 0 0 1em;
    margin: 0 0 0 1em;
}

ul.links li:first-child {
    border-left: 0;
    padding-left: 0;
    margin-left: 0;
}

/* Contact */

ul.contact {
    list-style: none;
    padding-left: 0;
}

ul.contact li {
    position: relative;
    border-top: solid 1px #ddd;
    padding: 1.3em 0 1.3em 7em;
}

ul.contact li h3 {
    position: absolute;
    left: 0;
    top: 1.3em;
    font-size: 1.1em;
}

ul.contact li p {
    margin: 0;
}

ul.contact li:first-child {
    padding-top: 0;
    border-top: 0;
}

ul.contact li:first-child h3 {
    top: 0;
}

/* Dates */

ul.dates {
    list-style: none;
    padding-left: 0;
}

ul.dates li {
    position: relative;
    border-top: solid 1px #ddd;
    padding: 1.3em 0 1.3em 6.75em;
}

ul.dates .date {
    display: block;
    position: absolute;
    left: 0;
    top: 1.3em;
    background-color: #d52349;
    height: 3.5em;
    text-align: center;
    color: #fff;
    line-height: 1em;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    padding: 0.5em 0.75em 0 1em;
}

ul.dates .date:after {
    content: '';
    position: absolute;
    bottom: 0;
    right: -1.2em;
    border-left: solid 1.25em #d52349;
    border-top: solid 1.8em transparent;
    border-bottom: solid 1.8em transparent;
}

ul.dates .date strong {
    display: block;
    font-size: 1.75em;
    padding-top: 0.15em;
}

ul.dates h3 {
    font-size: 1.1em;
}

ul.dates p {
    margin: 0;
}

ul.dates li:first-child {
    border-top: 0;
    padding-top: 0;
}

ul.dates li:first-child .date {
    top: 0;
}

/* Divided */

ul.divided {
    list-style: none;
    padding-left: 0;
}

ul.divided li {
    border-top: solid 1px #ddd;
    padding: 1em 0 1em 0;
}

ul.divided li:first-child {
    border-top: 0;
    padding-top: 0;
}

/* Social */

ul.social {
    list-style: none;
    padding-left: 0;
    overflow: hidden;
    cursor: default;
    margin: 0 0 3.25em 0;
}

ul.social li {
    display: inline-block;
    padding-left: 0;
    margin-left: 0.75em;
}

ul.social li:first-child {
    margin-left: 0;
}

ul.social li a {
    display: inline-block;
    width: 3em;
    height: 3em;
    text-align: center;
    border-radius: 5px;
    background: #888;
    -moz-transition: background-color .25s ease-in-out;
    -webkit-transition: background-color .25s ease-in-out;
    -ms-transition: background-color .25s ease-in-out;
    transition: background-color .25s ease-in-out;
}

ul.social li a:before {
    color: #fff;
    font-size: 2em;
    line-height: 1.5em;
}

ul.social li a.fa-facebook {
    background: #3c5a98;
}

ul.social li a.fa-facebook:hover {
    background: #4c6aa8;
}

ul.social li a.fa-twitter {
    background: #2daae4;
}

ul.social li a.fa-twitter:hover {
    background: #3dbaf4;
}

ul.social li a.fa-dribbble {
    background: #c4376b;
}

ul.social li a.fa-dribbble:hover {
    background: #d4477b;
}

ul.social li a.fa-linkedin {
    background: #006599;
}

ul.social li a.fa-linkedin:hover {
    background: #1075a9;
}

ul.social li a.fa-tumblr {
    background: #2b4661;
}

ul.social li a.fa-tumblr:hover {
    background: #3b5671;
}

ul.social li a.fa-google-plus {
    background: #da2713;
}

ul.social li a.fa-google-plus:hover {
    background: #ea3723;
}

/* Icons */

.icon {
    text-decoration: none;
    position: relative;
    text-decoration: none;
}

.icon:before {
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    display: inline-block;
    font-style: normal;
    font-variant: normal;
    text-rendering: auto;
    line-height: 1;
    text-transform: none !important;
    font-family: 'Font Awesome 5 Free';
    font-weight: 400;
}

.icon.solid:before {
    font-weight: 900;
}

.icon.brands:before {
    font-family: 'Font Awesome 5 Brands';
}

.icon:before {
    line-height: inherit;
}

.icon>.label {
    display: none;
}

.icon.featured {
    position: relative;
    display: inline-block;
    background-color: #d52349;
    width: 9em;
    padding: 1.75em 0 0.75em 0;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    margin: 0 0 4.5em 0;
    cursor: default;
}

.icon.featured:before {
    font-size: 4.5em;
    line-height: 1em;
    color: #fff;
}

.icon.featured:after {
    content: '';
    position: absolute;
    bottom: -1.95em;
    left: 0;
    border-top: solid 2em #d52349;
    border-left: solid 4.5em transparent;
    border-right: solid 4.5em transparent;
}

.icon.featured.alt {
    background-color: #252122;
}

.icon.featured.alt:after {
    border-top-color: #252122;
}

.icon.featured.alt2 {
    background-color: #827a7c;
}

.icon.featured.alt2:after {
    border-top-color: #827a7c;
}

/* Page Wrapper */

#page-wrapper>section {
    margin-bottom: 0;
}

/* Header */

#header {
    position: relative;
    background: #ffffff;
    border-bottom: solid 1px #ccc;
    padding: 5em 0 4em 0;
    text-align: center;
}

#header h1 {
    color: #252122;
    font-weight: 900;
    font-size: 2.5em;
    letter-spacing: -0.035em;
    line-height: 1;
}

/* Nav */

#nav {
    margin: 2.5em 0 0 0;
}

#nav>ul {
    margin: 0;
}

#nav>ul>li {
    display: inline-block;
    font-style: italic;
    margin: 0 0.35em 0 0.35em;
}

#nav>ul>li>ul {
    display: none;
}

#nav>ul>li>a {
    border-radius: 5px;
    color: #5d5d5d;
    text-decoration: none;
    padding: 0.6em 1.2em 0.6em 1.2em;
    -moz-transition: background-color .25s ease-in-out;
    -webkit-transition: background-color .25s ease-in-out;
    -ms-transition: background-color .25s ease-in-out;
    transition: background-color .25s ease-in-out;
    outline: 0;
}

#nav>ul>li:hover>a {
    background: #f3f3f3;
}

#nav>ul>li:active>a {
    background: #d52349;
    color: #fff !important;
    font-weight: 400;
}

.dropotron {
    border-radius: 5px;
    background-color: #252122;
    background-color: rgba(34, 30, 31, 0.98);
    padding: 1.25em 1.5em 1.25em 1.5em;
    font-style: italic;
    min-width: 13em;
    box-shadow: 0px 8px 15px 0px rgba(0, 0, 0, 0.5);
    text-align: left;
    margin-top: -1.25em;
    margin-left: -1px;
    list-style: none;
}

.dropotron a,
.dropotron span {
    color: #aaa;
    text-decoration: none;
    -moz-transition: color .25s ease-in-out;
    -webkit-transition: color .25s ease-in-out;
    -ms-transition: color .25s ease-in-out;
    transition: color .25s ease-in-out;
}

.dropotron li {
    padding: 0.25em 0 0.25em 0;
}

.dropotron li:hover>a,
.dropotron li:hover>span {
    color: #fff;
}

.dropotron.level-0 {
    margin-top: 2em;
}

.dropotron.level-0:before {
    content: '';
    display: block;
    position: absolute;
    left: 50%;
    margin-left: -10px;
    top: -9px;
    border-left: solid 10px transparent;
    border-right: solid 10px transparent;
    border-bottom: solid 10px #252122;
    border-bottom-color: rgba(34, 30, 31, 0.98);
}



/* Intro */

#intro {
    text-align: center;
    overflow: hidden;
}

#intro section {
    margin: 3em 0;
    padding: 2.5em 0;
}

#intro h2 {
    font-size: 1.75em;
}

#intro p {
    margin: 0;
}

#intro .middle {
    position: relative;
    z-index: 1;
}

#intro .middle:before {
    content: '';
    width: 32px;
    height: 100%;
    position: absolute;
    left: -24px;
    top: 0;
    display: block;
    z-index: -1;
    box-shadow: 32px 0 0 0 #f7f7f7, 0 -32px 0 0 #f7f7f7, 0 32px 0 0 #f7f7f7, 32px 32px 0 0 #f7f7f7, 32px -32px 0 0 #f7f7f7, 0 0 32px 0 rgba(0, 0, 0, 0.15);
}

#intro .middle:after {
    content: '';
    width: 32px;
    height: 100%;
    position: absolute;
    right: -24px;
    top: 0;
    display: block;
    z-index: -1;
    box-shadow: -32px 0 0 0 #f7f7f7, 0 -32px 0 0 #f7f7f7, 0 32px 0 0 #f7f7f7, -32px 32px 0 0 #f7f7f7, -32px -32px 0 0 #f7f7f7, 0 0 32px 0 rgba(0, 0, 0, 0.15);
}

#intro .button {
    min-width: 12em;
}

#intro footer {
    margin: 0;
}

