#main {
    position: relative;
    // background: #f7f7f7 url("images/bg02.png");
    padding: 4em 0 4em 0;
}

#main:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    // background: url("images/bg01.png");
    background-size: 100% 100%;
    z-index: 0;
    opacity: 0.1;
}

#main .major h2 {
    // background: #f7f7f7 url("images/bg02.png");
}

/* Footer */

#footer {
    position: relative;
    color: #858484;
    padding: 4em 0;
}

#footer:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    // background: url("images/bg01.png");
    background-size: 100% 100%;
    z-index: 0;
    opacity: 0.5;
}

#footer a {
    color: #c5c4c4;
}

#footer h2,
#footer h3,
#footer h4,
#footer h5,
#footer h6,
#footer strong,
#footer b {
    color: #fff;
}

#footer h2 a,
#footer h3 a,
#footer h4 a,
#footer h5 a,
#footer h6 a,
#footer strong a,
#footer b a {
