}

#footer header {
    margin: 0 0 3em 0;
}

#footer header h2 {
    font-size: 1.75em;
}

/* Copyright */

#copyright {
    text-align: center;
    margin: 3em 0 2em 0;
}

#copyright ul.links {
    display: inline-block;
    background: #2C2929;
    background: rgba(255, 255, 255, 0.05);
    border-radius: 5px;
    margin: 0;
    padding: 0.85em 2.25em 0.85em 2.25em;
}

/* XLarge */

@media screen and (max-width: 1680px) {

    /* Basic */

    body,
    input,
    textarea,
    select {
        font-size: 12pt;
    }

}

/* Large */

@media screen and (max-width: 1280px) {

    /* Basic */

    body,
    input,
    textarea,
    select {
        font-size: 11pt;
    }

    /* Section/Article */

    header.major {
        margin: 0.75em 0 2.5em 0;
    }

    header.major h2 {
        padding: 0 1em 0 1em;
    }

    /* Social */

    ul.social li {
        margin: 0 0 0.25em 0.75em;
    }

    /* Header */

    #header {
        padding: 4em 0 3em 0;
    }

    /* Banner */

    #banner {
        padding: 6em 0;
        margin: 3em 0 0 0;
    }

    /* Intro */

    #intro {
        padding: 0;
    }

    #intro section {
        padding: 1em 2em;
    }

    #intro .middle:before {
        left: -12px;
    }

    #intro .middle:after {
        right: -12px;
    }

    /* Main */

    #main {
        padding: 30px 0;
    }

    /* Footer */

    #footer {
        padding: 3em 0;
    }

    #footer:before {
        height: 2.5em;
    }

    #footer header {
        margin: 0 0 1.5em 0;
    }

    #footer header h2 {
        font-size: 1.25em;
    }

    /* Copyright */

    #copyright {
        margin: 0;
    }

}

/* Medium */

#navPanel,
#titleBar {
    display: none;
}

@media screen and (max-width: 980px) {

    /* Basic */

    html,
    body {
        overflow-x: hidden;
    }

    /* Box */

    .box footer ul.actions li {
        margin: 1em 0 0 0;
        display: block;
    }

    .box footer ul.actions li .button {
        width: 100%;
    }

    .box footer ul.actions li:first-child {
        margin-top: 0;
    }

    /* Header */

    #header {
        padding: 3.5em 0 3em 0;
    }

    /* Nav */

    #nav {
        display: none;
    }

    /* Intro */

    #intro {
        padding: 3em 0 0 0;
    }

    #intro section {
        position: relative;
        border-top: solid 1px #dfdfdf;
        margin: 0;
        padding: 40px 0 0 0;
    }

    #intro section.first {
        border-top: 0;
        padding-top: 0;
    }

    #intro section p {
        margin: 0;
    }

    #intro .middle:before,
    #intro .middle:after {
        display: none;
    }

    #intro footer {
        padding: 3em 0 0 0;
    }

    #intro footer ul.actions {
        margin-bottom: 0;
    }

    /* Main */

    #main {
        padding: 40px 0;
    }

    /* Copyright */

    #copyright ul.links {
        display: block;
    }

    /* Nav */

    #page-wrapper {
        -moz-backface-visibility: hidden;
        -webkit-backface-visibility: hidden;
        -ms-backface-visibility: hidden;
        backface-visibility: hidden;
        -moz-transition: -moz-transform 0.5s ease;
        -webkit-transition: -webkit-transform 0.5s ease;
        -ms-transition: -ms-transform 0.5s ease;
        transition: transform 0.5s ease;
        padding-bottom: 1px;
    }

    #titleBar {
        -moz-backface-visibility: hidden;
        -webkit-backface-visibility: hidden;
        -ms-backface-visibility: hidden;
        backface-visibility: hidden;
        -moz-transition: -moz-transform 0.5s ease;
        -webkit-transition: -webkit-transform 0.5s ease;
        -ms-transition: -ms-transform 0.5s ease;
        transition: transform 0.5s ease;
        display: block;
        height: 44px;
        left: 0;
        position: fixed;
        top: 0;
        width: 100%;
        z-index: 10001;
        background: none;
    }

    #titleBar .toggle {
        text-decoration: none;
        width: 80px;
        height: 60px;
    }

    #titleBar .toggle:before {
        -moz-osx-font-smoothing: grayscale;
        -webkit-font-smoothing: antialiased;
        display: inline-block;
        font-style: normal;
        font-variant: normal;
        text-rendering: auto;
        line-height: 1;
        text-transform: none !important;
        font-family: 'Font Awesome 5 Free';
        font-weight: 900;
    }

    #titleBar .toggle:before {
        content: '\f0c9';
        display: block;
        position: absolute;
        left: 10px;
        top: 10px;
        width: 50px;
        height: 40px;
        line-height: 40px;
        font-size: 18px;
        color: #fff;
        text-align: center;
        background: rgba(150, 150, 150, 0.75);
        border-radius: 5px;
    }

    #titleBar .toggle:active:before {
        background-color: #969696;
    }

    #navPanel {
        -moz-backface-visibility: hidden;
        -webkit-backface-visibility: hidden;
        -ms-backface-visibility: hidden;
        backface-visibility: hidden;
        -moz-transform: translateX(-275px);
        -webkit-transform: translateX(-275px);
        -ms-transform: translateX(-275px);
        transform: translateX(-275px);
        -moz-transition: -moz-transform 0.5s ease;
        -webkit-transition: -webkit-transform 0.5s ease;
        -ms-transition: -ms-transform 0.5s ease;
        transition: transform 0.5s ease;
        display: block;
        height: 100%;
        left: 0;
        overflow-y: auto;
        position: fixed;
        top: 0;
        width: 275px;
        z-index: 10002;
        color: #858484;
        // background-image: url("images/bg02.png");
        box-shadow: inset -3px 0px 5px 0px rgba(0, 0, 0, 0.35);
    }

    #navPanel .link {
        display: block;
        color: #888;
        text-decoration: none;
        height: 44px;
        line-height: 44px;
        border-top: solid 1px rgba(255, 255, 255, 0.05);
        padding: 0 1em 0 1em;
        font-style: italic;
    }

    #navPanel .link:first-child {
        border-top: 0;
    }

    #navPanel .indent-1 {
        display: inline-block;
        width: 1em;
    }

    #navPanel .indent-2 {
        display: inline-block;
        width: 2em;
    }

    #navPanel .indent-3 {
        display: inline-block;
        width: 3em;
    }

    #navPanel .indent-4 {
        display: inline-block;
        width: 4em;
    }

    #navPanel .indent-5 {
        display: inline-block;
        width: 5em;
    }

    #navPanel .depth-0 {
        color: #fff;
    }

    body.navPanel-visible #page-wrapper {
        -moz-transform: translateX(275px);
        -webkit-transform: translateX(275px);
        -ms-transform: translateX(275px);
        transform: translateX(275px);
    }

    body.navPanel-visible #titleBar {
        -moz-transform: translateX(275px);
        -webkit-transform: translateX(275px);
        -ms-transform: translateX(275px);
        transform: translateX(275px);
    }

    body.navPanel-visible #navPanel {
        -moz-transform: translateX(0);
        -webkit-transform: translateX(0);
        -ms-transform: translateX(0);
        transform: translateX(0);
    }

}

/* Small */

@media screen and (max-width: 736px) {

    /* Basic */

    body,
    input,
    textarea,
    select {
        font-size: 11pt;
    }

    h2,
    h3,
    h4,
    h5,
    h6 {
        font-size: 1.25em;
    }

    /* Button */

    input[type="button"].large,
    input[type="submit"].large,
    input[type="reset"].large,
    button.large,
    .button.large {
        font-size: 1.1em;
        padding: 0.65em 1.5em 0.65em 1.5em;
    }

    /* Actions */

    ul.actions li {
        margin: 1em 0 0 0;
        display: block;
    }

    ul.actions li .button {
        width: 100%;
    }

    ul.actions li:first-child {
        margin-top: 0;
    }

    /* Section/Article */

    section,
    article {
        margin-bottom: 20px;
    }

    header {
        margin: 0 0 1em 0;
    }

    footer {
        margin: 2em 0 0 0;
    }

    /* Box */

    .box {
        padding: 25px 15px 20px 15px;
    }

    .box .image.featured {
        left: 15px;
        top: 15px;
        margin: -40px 0 40px -30px;
    }

    .box.post header {
        padding-top: 0;
        margin: 0 0 1em 0;
    }

    .box.post h2 {
        font-size: 1.5em;
        line-height: 1.5em;
    }

    .box.post header>p {
        margin-top: 0;
        font-size: 1em;
    }

    /* Divided */

    ul.divided li {
        padding: 0.5em 0 0.5em 0;
    }

    /* Contact */

    ul.contact li {
        padding: 0.8em 0 0.8em 7em;
    }

    ul.contact li h3 {
        top: 0.8em;
    }

    /* Dates */

    ul.dates li {
        padding-left: 6em;
    }

    ul.dates h3 {
        margin: 0 0 0.5em 0;
    }

    /* Links */

    ul.links li {
        display: block;
        padding: 0.25em 0 0 0;
        margin: 0.25em 0 0 0;
        border-left: 0;
    }

    ul.links li:first-child {
        padding-top: 0;
        margin-top: 0;
    }

    /* Social */

    ul.social li {
        margin: 0 0.5em 0.75em 0;
    }

    /* Icons */

    .icon.featured {
        cursor: pointer;
        width: 8em;
        margin: 0 0 3em 0;
    }

    .icon.featured:after {
        border-left: solid 4em transparent;
        border-right: solid 4em transparent;
    }

    /* Header */

    #header {
        text-align: center;
        padding: 2em 0;
    }

    #header h1 {
        color: #252122;
        font-weight: 900;
        font-size: 1.75em;
        letter-spacing: -0.035em;
    }

    /* Intro */

    #intro {
        padding: 2em 0 0 0;
    }

    #intro section {
        padding: 40px 0 0 0;
        margin: 20px 0 0 0;
    }

    #intro h2 {
        font-size: 1.25em;
    }

    /* Banner */

    #banner {
        padding: 6em 0;
        margin: 2em 0 0 0;
    }

    #banner header {
        padding: 1.5em 1em 1.5em 1em;
        display: block;
        border-radius: 0;
    }

    #banner header h2 {
        font-size: 1.5em;
        line-height: 1.5em;
        margin: 0 0 0.25em 0;
    }

    #banner header p {
        font-size: 1.2em;
    }

    /* Main */

    #main {
        padding: 20px 0;
    }

    /* Footer */

    #footer {
        padding: 40px 0 20px 0;
    }

}